import { connect } from "react-redux";
import Card from "../cards/card";

import "./menu.css";
import MenuBG from "../../images/menuBG.jpg";
import Menu1 from "../../images/menu1.png";
import Menu2 from "../../images/menu2.png";
import Menu3 from "../../images/menu3.png";
import Menu4 from "../../images/menu4.png";
import Menu5 from "../../images/menu5.png";
import Menu6 from "../../images/menu6.png";
import Menu7 from "../../images/menu7.png";
import Menu8 from "../../images/menu8.png";
import Menu9 from "../../images/menu9.png";

function Menu(props) {
  let menu = [Menu1, Menu2, Menu3, Menu4, Menu5, Menu6, Menu7, Menu8, Menu9];

  let style = {
    backgroundImage: `url(${MenuBG})`,
  };

  // let menu = props.menuCategories;

  return (
    // <LoadingOverlay active={true} spinner text="Loading your content...">
    <div className="menu" style={style}>
      <div className="menuHeader">
        <span className="menuText">Menu</span>
        <span className="menuTagLine">To keep your Tummy tingling ...</span>
        <div> </div>
      </div>
      <div className="menusContainer">
        {menu.map((page, key) => {
          return (
            <div key={key} className="menuPageContainer">
              <img src={page} className="menuPage" alt={`page` + (key + 1)} />
            </div>
          );
        })}
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    menuCategories: state.menuReducers.menuCategories,
  };
};
export default connect(mapStateToProps)(Menu);
