import "./specialOffers.css";
import { useState, useEffect } from "react";
// import ReactCardFlip from "react-card-flip";
import Slider from "infinite-react-carousel";
import Offer1 from "../../images/offer1.jpg";
import Offer2 from "../../images/offer2.jpg";
import Offer3 from "../../images/offer3.jpg";
import Offer4 from "../../images/offer4.jpg";
import Offer5 from "../../images/offer5.jpg";
import Offer6 from "../../images/offer6.jpg";
import Offer7 from "../../images/offer7.jpg";
import Reserve1 from "../../images/reserve1.jpg";
import Reserve2 from "../../images/reserve2.jpg";

function SpecialOffers(props) {
  return (
    <div className="sectionContainer">
        <Slider slidesToShow={3} autoplay arrows={false} centerMode={true} className='offersContainer'>
            <img className="reserveFront" src={Reserve1} alt="reserve" />
            <img className="reserveFront" src={Reserve2} alt="reserve" />
            <img className="reserveFront" src={Offer1} alt="reserve" />
            <img className="reserveFront" src={Offer2} alt="reserve" />
            <img className="reserveFront" src={Offer3} alt="reserve" />
            <img className="reserveFront" src={Offer4} alt="reserve" />
            <img className="reserveFront" src={Offer5} alt="reserve" />
            <img className="reserveFront" src={Offer6} alt="reserve" />
            <img className="reserveFront" src={Offer7} alt="reserve" />
        </Slider>
    </div>
  );
}

export default SpecialOffers;
